const ExamPage = () => {
  const navigate = useNavigate()
  if (localStorage.getItem('accessToken')==null){
    navigate('/login')
  }
  const initialTimeLeft = () => {
    const savedTime = localStorage.getItem('timeLeft');
    return savedTime !== null ? parseInt(savedTime) : 20 * 60;
  };

  const [timeLeft, setTimeLeft] = useState(initialTimeLeft);
  const [showModal, setShowModal] = useState(false);
  const [showToast, setShowToast] = useState(false);

  useEffect(() => {
    const timerId = setInterval(() => {
      setTimeLeft(prevTime => {
        if (prevTime > 0) {
          const newTime = prevTime - 1;
          localStorage.setItem('timeLeft', newTime);
          if (newTime === 60) { // Thng bo khi cn 1 pht
            setShowToast(true);
          }
          return newTime;
        } else {
          clearInterval(timerId);
          setShowToast(true);
          return 0;
        }
      });
    }, 1000);

    return () => clearInterval(timerId);
  }, []);

  const handleSubmit = () => {
    setShowModal(true);
  };

  const handleConfirmSubmit = () => {
    setShowModal(false);
    alert('Bn  np bi thnh cng!');
  };

  const handleCloseModal = () => setShowModal(false);

  const renderQuestions = () => {
    return questions.map((q, index) => (
      <div key={index} className="question">
        <h5>Cu {index + 1}: {q.question}</h5>
        {q.options.map((option, i) => (
          <label key={i}>
            <input type="radio" name={`q${index}`} value={`option${i}`} />
            {String.fromCharCode(65 + i)}: {option}
          </label>
        ))}
        <br />
      </div>
    ));
  };

  const scrollToQuestion = (index) => {
    document.querySelectorAll('.question')[index].scrollIntoView({ behavior: 'smooth' });
  };

  const renderQuestionNumbers = () => {
    return questions.map((_, index) => (
      <div key={index} className="question-number" onClick={() => scrollToQuestion(index)}>
        {index + 1}
      </div>
    ));
  };
  const checkAnswered = ()=> {
    const radioButtons = document.querySelectorAll('input[type="radio"]');
    radioButtons.forEach((radio, index) => {
        radio.addEventListener('change', () => {
            const questionNumber = Math.floor(index / 4);
            const questionNumberElement = document.querySelectorAll('.question-number')[questionNumber];
            questionNumberElement.classList.add('answered');
        });
    });
}

  const minutes = Math.floor(timeLeft / 60);
  const seconds = timeLeft % 60;
  checkAnswered();
  return (
    <div className="container-fluid">
      <div className="row">
        <div id="questions" className="col-12 col-lg-8">
          <div id="container">
            <h1>Bi Thi</h1>
            <div id="question-container">
              {renderQuestions()}
            </div>
          </div>
        </div>
        <div id="sidebar" className="col-12 col-lg-4">
          <div id="sidebar-content">
            <div id="timer">
              <div id="timer-text">Thi gian cn li:</div>
              <div id="timer-time">{`${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`}</div>
            </div>
            <hr />
            <div id="saveButton" onClick={handleSubmit}>NP BI</div>
            <div className="note">
              *Lu : Bn ch c hon thnh bi thi ca mnh trong thi gian cho php, qu thi gian quy nh h thng s dng bi lm ca bn v np bi
            </div>
            <div id="question-list">
              {renderQuestionNumbers()}
            </div>
          </div>
        </div>
      </div>
      <button id="scrollToBottomBtn" title="Cun xung cui trang" onClick={() => window.scrollTo({ top: document.body.scrollHeight, behavior: 'smooth' })}>
        NP BI 
      </button>

      {/* Modal xc nhn np bi */}
      <Modal show={showModal} onHide={handleCloseModal} >
        <Modal.Header closeButton>
          <Modal.Title>Xc Nhn</Modal.Title>
        </Modal.Header>
        <Modal.Body>Bn c chc chn mun np bi khng?</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleCloseModal}>
            Hy
          </Button>+
          <Button variant="primary" onClick={handleConfirmSubmit}>
            Xc Nhn
          </Button>
        </Modal.Footer>
      </Modal>

      {/* Toast thng bo */}
      <ToastContainer position="top-end" className="p-3" itemType='info'>
        <Toast show={showToast} onClose={() => setShowToast(false)} delay={5000} autohide>
          <Toast.Body>Sp Ht gi! Bn nn np bi ngay.</Toast.Body>
        </Toast>
      </ToastContainer>
    </div>
  );
};

export default ExamPage;